/* List */

.list{
    margin-bottom: 30px;
}

.list-feature{
    @extend .list;
    margin: 0;
    padding: 0;
    width: 80%;
    
    li{
        line-height: 3.6em;
    @include add-border(1px, #cccccc, bottom);
    }
    
    li:last-child{
        border: 0;
    }
    
    span{
        vertical-align: top;
        padding-top: 9px;
        font-size: 120%; 
    }
    
    i{
        margin-top: -9px;
        margin-right: 5px;
    }
}

.list-comments{
    padding: 0;
    li{
        padding: 10px 0 5px 0;
        @include add-border(1px, #cccccc, bottom);
        overflow: auto;
        vertical-align: middle;
        
        &:last-child{
            border: 0;
        }
        
        img{
            width: 100px;
            padding: 10px;
        }
        
        p{
            width: auto;
            margin: 10px auto;
            
        }
    }
}

/* Icons */

.fa-6{
    font-size: 40px;
}

.fa-primary{
    color: $primary-color;

}

.fa-border{
    @include add-border(1px,$primary-color, all);
    @include border-radius(5px);
    padding: 13px 15px;
    width: 70px;
    margin-bottom: 10px; 
}

/* Headings */

.heading-primary{
    color: $primary-color;
}

.heading-primary-a{
    @extend .heading-primary;
    @include add-border(2px, $primary-color, bottom);
}

.heading-secondary{
    color: $secondary-color;
}

.heading-secondary-a{
    @extend .heading-secondary;
    @include add-border(2px, $secondary-color, bottom);
}


.heading-light{
    color: $light-color;
}


.heading-dark{
    color: $dark-color;
}

/* Emphasis text */

.em-primary{
    color: $primary-color;
}

.em-secondary{
    color: $secondary-color;
}

.em-light{
    color: $light-color;
}

.em-dark{
    color: $dark-color;
}