/* Blog */

.blog-post{
    @include add-border(1px, #cccccc, right-left);
    @include add-border(1px, #cccccc, top);
    padding: 15px;
    
    .page-header{
        margin-top: 5px;
    }
}

.blog-featured{
    float: left;
    margin-right: 15px;
    padding: 10px;
 
}

.post-meta{
    background: #f3f3f3;
    @include add-border(1px,#cccccc, all);
    margin-bottom: 20px;

    padding: 10px 10px 5px 10px;
}